import React, { useState, useEffect } from 'react';
import axios from 'axios';
import ProductCarousel from './productCarousel';
import SubcatCarousel from './subcatCarousel';
import Navbar from './navbar';
import "./editBB.css"

const Darryl = ({handleEditPress}) => {
  const [categories, setCategories] = useState([]);
  const [balloons, setBalloons] = useState([]);

  const [activeCategory, setCategory] = useState(0);
  const [activeSubcategory, setSubcategory] = useState(0);
  const [prevBalloon, setPrevBalloon] = useState(null);
  const [activeBalloon, setActiveBalloon] = useState(null);
  const [fetched, setFetched] = useState(false);

  
  // useEffect(() => {
  //   fetchCategories();
  // }, []);

  useEffect( () => {
    if (!fetched) {
      fetchCategories();
      setFetched(true);
    } else {
      const currActive = document.getElementById(`active-product-${prevBalloon}`);
      const newActive = document.getElementById(`active-product-${activeBalloon}`)
      
      currActive.lastChild.style.display = "none";
      currActive.style.border = "1px solid #E4E4E4"

      newActive.lastChild.style.display = "block";
      newActive.style.border = "2px solid #009CDB"

      setPrevBalloon(activeBalloon)
    }
  }, [activeBalloon, activeCategory]);

  const fetchCategories = async () => {
    const response = await axios.get(
      `https://peaceful-stream-16724.herokuapp.com/https://test-web.partycity.com/s/Sites-partycity_us2-Site/dw/shop/v20_4/categories/balloons?client_id=f75472a2-de04-4b73-80e1-32a261e83281&levels=3`
    );
    const cats = response.data.categories.slice(0, 3);   
    cats[0].categories.unshift({ name: "Sample Balloons", id: "sample-balloons" })
    cats[1].categories.splice(0,5).pop();
    cats[1].categories[1].name = 'CONGRATS BALLOONS'
    cats[1].categories[3].name = 'WELCOME BALLOONS'
    cats[2].categories.shift()
    
    await setCategories(cats);
    await onSubcatSelection(0, 'sample-balloons');
    await setPrevBalloon(0);
    await setActiveBalloon(0);
  }

  const fetchBalloons = async (cgid) => {
    if (cgid === 'sample-balloons') {
      const sample = [{ product_name: "Sample Balloon", price: "9.99", represented_product: { id: 47094 }}, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 863280} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 660902} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 661115} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 662678} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 686233} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 740789} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 777434} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 794372} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 846715} }, { product_name: "Sample Balloon", price: "9.99", represented_product: { id: 863280} }]

      await setBalloons(sample);
    } else {
      const response = await axios.get(
        `https://peaceful-stream-16724.herokuapp.com/https://test-web.partycity.com/s/Sites-partycity_us2-Site/dw/shop/v20_4/product_search?client_id=f75472a2-de04-4b73-80e1-32a261e83281&count=7&refine_1=cgid=${cgid}&expand=images, prices`
        // `https://peaceful-stream-16724.herokuapp.com/https://test-web.partycity.com/s/Sites-partycity_us2-Site/dw/shop/v20_4/product_search?client_id=f75472a2-de04-4b73-80e1-32a261e83281&count=200&refine_1=cgid=${cgid}&refine_2=c_kioskOnly=true&expand=images, prices`
      );

      await setBalloons(response.data.hits);
    }
  }

  const onCategorySelection = async (catIdx) => {
    const oldActive = document.getElementById(`nav${activeCategory}`);
    oldActive.style.opacity = ".4";

    const newActive = document.getElementById(`nav${catIdx}`)
    newActive.style.opacity = "1";

    await setCategory(catIdx);
    await onSubcatSelection(0, categories[catIdx].categories[0].id);
    await setPrevBalloon(0);
    await setActiveBalloon(0);
  }

  const onSubcatSelection = (idx, cgid) => {
    console.log('prev', activeSubcategory);
    console.log('idx', idx)
    const currActive = document.getElementById(`sslide-${activeSubcategory}`);
    const newActive = document.getElementById(`sslide-${idx}`);

    currActive.style.background = '#FFFFFF'
    newActive.style.background = 'linear-gradient(180deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%), #009CDB'
    
    setSubcategory(idx);
    fetchBalloons(cgid);
  }

  return (
    <div className="BB">
      <div className="navbar">
        <Navbar
          categories={categories}
          activeCategory={activeCategory}
          handleEditPress={() => handleEditPress()}
          onCategorySelection={(catIdx) => onCategorySelection(catIdx)}
        />
      </div>
      <div className="bottom-drawer">
        <div className="subcat-carousel">
          <SubcatCarousel
            key={activeCategory}
            subcategories={categories[activeCategory]}
            activeSubcategory={activeSubcategory}
            onSubcatSelection={(cgname, cgid) => onSubcatSelection(cgname, cgid)}
          />
        </div>
        <div className="product-carousel">
          <ProductCarousel 
            key={activeCategory}
            balloons={balloons} 
            categories={categories} 
            // onBalloonSelection={(e) => onBalloonSelection(e)}
            setActiveBalloon={setActiveBalloon}
          />
        </div>
      </div>
    </div>
  );
}

export default Darryl;


